#root {position: relative; max-width: 500px; margin: auto;}

.loading { animation: load 1s linear infinite; position: absolute; top: 50%; left: 50%; transform: translate(-50%,-50%); width: 30px; aspect-ratio: auto 1/1; background-color: lightblue; border-radius: 50%; }

@keyframes load {
    0% {width: 30px;}
    50% {width: 70px;}
    100% {width: 30px;}
}

header {
    
    display: flex; align-items: center; height: 60px; width: 100%; padding: 8px 15px; background: url(./assets/wood_bg.jpg) no-repeat center center / cover;

    .searchBox {
        display: flex; align-items: center; justify-content: space-between; width: 100%; height: 100%; border-radius: 10px; background-color: white; padding: 0 15px;
        
        input {display: inline-block; width: calc(100% - 35px); font-size: 15px;}
        
        button {min-width: 22px; width: 22px; height: 22px; background: url(./assets/ico_search.png) no-repeat center center / 100% auto;}
    }
    
    
}

#titleBox {
    
    display: flex; align-items: center; justify-content: space-between; height: 60px; padding: 0 20px; box-shadow: 0 0 0.5em rgba($color: #000000, $alpha: 0.15); 
    
    .back { display: block; min-width: 23px; width: 23px; height: 23px; background: url(./assets/ico_back.png) no-repeat center center / 100% auto;}
    
    .right {display: flex; align-items: center; gap:0 8px; font-size: 15px; font-weight: 500;}
    
}

#home {
    
    header {
        input {pointer-events: none;}
    }
    
    .listBox {
        height: calc(100dvh - 60px); padding: 20px 15px; background-color: #f7f7f7;
        
        ul {
           display: flex; flex-wrap: wrap; justify-content: space-between;
        }
        .readbook {
            position: relative; width: 48.5%; padding-top: 20px; margin-bottom: 20px; border-radius: 5px; background-color: #fff; overflow: hidden; box-shadow: 0 0 9px #e7e7e7; z-index: 1;
            
            &::after {content: ""; position: absolute; left: 0; bottom: 0; width: 100%; height: 45%; background: url(./assets/wood_bg.jpg) no-repeat center bottom / cover; z-index: -1;}
            
            .imgBox {
                position: relative; width: fit-content; margin: auto; box-shadow: -1px 2px 4px rgba(0, 0, 0, 0.6);
                
                &::after {content: ""; position: absolute; top: 0; left: 0; width: 30%; height: 100%; background: linear-gradient(to right, #1e1e1e6e, #c1c1c1a3, #ffffff00 25%);}
                &::before {content: ""; position: absolute; bottom: -5px; left: 50%; transform: translateX(-50%); width: 70%; height: 17px; border-radius: 3px 3px 0 0; box-shadow: -1px -3px 5px rgba(0, 0, 0, 0.6); background: url(./assets/wood_bg.jpg) no-repeat center center / cover;}
                
                img {height: 150px;}
            }
            
            p { display: block; width: calc(100% - 30px); margin: 15px auto 0; padding-bottom: 15px; overflow: hidden; font-size: 12px; font-weight: 600; text-overflow: ellipsis; white-space: nowrap; text-align: center; color: white; }
            
        }
        
        
        .emptyList { display: flex; align-items: center; justify-content: center; width: 100%; height: 100%; font-size: 18px; color: #ccc;}
        
    }
}

#search {
    header {
        .back {display: block; margin-right: 15px; min-width: 23px; width: 23px; height: 23px; background: url(./assets/ico_back_w.png) no-repeat center center / 100% auto;}
    }
    .listBox {
        
        padding: 20px;
        
        .list {
            display: flex; align-items: center; width: 100%;
            
            .imgBox {
                height: 110px;
                img {height: 100%;}
            }
            
            .txtBox {
                flex-grow: 1; display: flex; flex-wrap: wrap; justify-content: space-between; padding-left: 20px; text-align: left;
                
                p.title {display: block; width: 100%; margin-bottom: 10px; font-size: 16px; font-weight: 500; word-break: keep-all; }
                p.author {font-size: 13px;}
                p.date {font-size: 13px; color: #888;}
            }
            
            & + .list {
                padding-top: 20px; margin-top: 20px; border-top: 1px solid #eee;
            }
        }
        
    }
}


#Bookinfo {
    
    .infoBox {
        
        padding: 30px 20px 40px;
        
        .bookBox {
            
            strong { display: block; margin-bottom: 20px; font-size: 20px; font-weight: 500; word-break: keep-all;}
            
            .imgBox {
                position: relative; width: fit-content; margin: auto; box-shadow: -1px 2px 4px rgba(0, 0, 0, 0.6);
                
                &::after {content: ""; position: absolute; top: 0; left: 0; width: 30%; height: 100%; background: linear-gradient(to right, #1e1e1e6e, #c1c1c1a3, #ffffff00 25%);}
                
                img {height: 170px;}
            }
            
            p {display: block; margin-top: 20px; font-size: 15px;}
        }
        
        .txtBox {
            text-align: left; padding-top: 25px; margin-top: 25px; border-top: 1px solid #eaeaea;
            p {font-size: 14px; margin-bottom: 15px; color: #777;}
            span {display: block; margin-bottom: 3px; font-weight: 600;}
        }
    }
    
}

#edit {
    
    .inner { padding: 30px 20px 50px;}
    
    .bookBox {
            
        strong { display: block; margin-bottom: 20px; font-size: 20px; font-weight: 500; word-break: keep-all;}
        
        .imgBox {
            position: relative; width: fit-content; margin: auto; box-shadow: -1px 2px 4px rgba(0, 0, 0, 0.6);
            
            &::after {content: ""; position: absolute; top: 0; left: 0; width: 30%; height: 100%; background: linear-gradient(to right, #1e1e1e6e, #c1c1c1a3, #ffffff00 25%);}
            
            img {height: 170px;}
        }
        
        p {display: block; margin-top: 20px; font-size: 15px;}
    }
    
    
    .inputBox {
        padding-top: 25px; margin-top: 25px; border-top: 1px solid #eaeaea;
        
        li {
            display: flex; align-items: center; margin-bottom: 25px;
            
            p {margin-right: 5px; font-size: 15px; font-weight: 500;}
            
            .react-datepicker-wrapper {position: relative; width: 100%;}
            .react-datepicker__tab-loop {position: absolute;}
            .datePicker,
            textarea {width: 100%; height: 40px; padding: 0 15px; border:1px solid #eaeaea; border-radius: 5px; font-size: 14px;}
            textarea {height: 120px; padding: 15px; resize: none; }
            textarea::placeholder {color: #ccc;}
            
            .flexBox {
                display: flex; align-items: center; justify-content: space-between; width: 100%;
                
                span {
                    font-size: 15px;
                    small {font-size: inherit; font-weight: 500; color: royalblue;}
                }
            }
            
            &.starBox {
                display: flex; align-items: center; gap:0 7px;
                .ico_star {display: inline-flex;}
                .star {  fill: #e4a317; font-size: 22px;}
            }
            
            &.col {
                flex-direction: column; align-items: flex-start; gap: 8px;
                small {margin-bottom: 3px;}
                span.txt {text-align: left; font-size: 13px;}
            }
        }
        
        
    }
    
    .delateBtn { display: inline-block; margin: 25px auto 0; text-align: center; color: #888; font-size: 13px;  }
    
    
}
